import React from 'react'
import { Link } from 'react-router-dom'

export default function Footer(props) {
  return (
    <div>
        <div className="container ">
  <footer className="py-0">
    <ul className="nav justify-content-center border-bottom pb-3 mb-3">
      <li className="nav-item"><Link to="home" className="nav-link px-2 text-white-50 ">Home</Link></li>
      <li className="nav-item"><Link to="movies" className="nav-link px-2 text-white-50">Movies</Link></li>
      {/* <li className="nav-item"><Link to="#" className="nav-link px-2 text-white-50">Pricing</Link></li> */}
      <li className="nav-item"><Link to="contacts" className="nav-link px-2 text-white-50">FAQs</Link></li>
      <li className="nav-item"><Link to="about" className="nav-link px-2 text-white-50">About</Link></li>
    </ul>
    <p className="text-center text-white-50 ">© 2023 Company, Inc</p>
  </footer>
</div>
      
      {/* <footer className='position-fixed start-0 end-0 bottom-0 p-2 bg-dark text-center text-white'>footer</footer> */}
    </div>
  )
}
