import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

export default function MovieDetails() {

    let params = useParams();

    const [movieDetails, setMovieDetails] = useState(null);
    async function getMovieDetails(id)
    {
        let {data} = await axios.get(`https://api.themoviedb.org/3/movie/${id}?api_key=7498df51a2f5c06927536a5aa88482bd&language=en-Us`);
        setMovieDetails(data);
    }

    useEffect(()=> {
        getMovieDetails(params.id)
    }, [])

  return (
    <>
    {movieDetails? <div className="row">
        <div className="col-md-3">
        <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + movieDetails.poster_path}/>
        <button className='btn btn-outline-warning  w-100 my-1 p-2' ><Link to={movieDetails.homepage}>{movieDetails.original_title}</Link></button>
        </div>
        <div className="col-md-9">
            <h2>{movieDetails.title}</h2>
            <p className='text-white-50 py-3'>{movieDetails.overview}</p>
            <ul className='m-0 p-0'>
                {/* <li>original_title : {movieDetails.original_title}</li> */}
                <li>release_date : {movieDetails.release_date}</li>
                <li>budget : {movieDetails.budget}</li>
                <li>revenue : {movieDetails.revenue}</li>
                <li>vote : {movieDetails.vote_average}</li>
                <li>popularity : {movieDetails.popularity}</li>
                <li>vote_count : {movieDetails.vote_count}</li>
                {/* <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + movieDetails.backdrop_path}/> */}

                {/* <li>homepage : {movieDetails.homepage}</li> */}
            </ul>
        </div>
    </div>:<div className='vh-100 d-flex align-items-center justify-content-center'>
        <i className='fas fa-spinner fa-spin fa-3x'></i>
        </div>}
    </>
  )
}
