// import React from 'react'
import axios from 'axios';
import Joi from 'joi';
import React, { useState } from 'react'
import { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

export default function Login(props) {

  let navigate = useNavigate();
  const [isLoading, setIsLoading] = useState(false)
  const [errorList, setErrorList] = useState([])
  const [error, setError] = useState('')
  let [user , setUser] = useState({
    email:'',
    password:''
  });

  function getUserData(e){
    let myUser = {...user};
    myUser[e.target.name] = e.target.value ;
    setUser(myUser);
    // console.log(myUser)
  }

  async function submitLoginForm(e){
    
    e.preventDefault();
    setIsLoading(true)
    let validationResult = validateLoginForm();
    if (validationResult.error)
    {
      setErrorList(validationResult.error.details)
      setIsLoading(false)
    }
    else
    {

      let {data} = await axios.post('https://route-egypt-api.herokuapp.com/signin' , user);
      
      if (data.message === 'success') // Logination tmam
      {
        setIsLoading(true)
        localStorage.setItem('userToken', data.token)
        // saveUserData
        props.saveUserData();
        // navigato login || Home
        navigate('/home')
      }
      else{
        setError(data.message)
        setIsLoading(true)
  
      }
    }

  }

  function validateLoginForm()
  {
    let scheme = Joi.object({
      email : Joi.string().email({ minDomainSegments: 2, tlds: { allow: ['com', 'net'] } }).required(),
      password : Joi.string().pattern(new RegExp('^[A-Z][a-z]{3,10}$')).required(),
    })

    return scheme.validate(user , {abortEarly:false});
  }

  useEffect(()=>{
    if (localStorage.getItem('userToken'))
    {
      navigate('/home');
    }
  },[]);

  return (
    <>
    <div className="w-75 mx-auto">
      <h2>Login Now</h2>
      {errorList.map((error, i)=>  user.password?<div key={i} className="alert py-2 alert-danger">password invalid</div> : <div className="alert py-2 alert-danger">{error.message}</div>)}
      {error.length>0 ?<div className="alert py-2 alert-danger">{error}</div> : ''}
      
      <form onSubmit={submitLoginForm}>
        <label htmlFor='email'>email : </label>
        <input onChange={getUserData} type='email' className='form-control mb-2' id='email' name='email'/>

        <label htmlFor='password'>password : </label>
        <input onChange={getUserData} type='password' className='form-control mb-2' id='password' name='password'/>

        <button type='submit' className='btn btn-outline-info'>
          {isLoading === true ? <i className='fas fa-spinner fa-spin'></i> : 'Login'}
        </button>
      </form>
    </div>
    </>
  )
}
