import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

export default function PeopleDetails() {

    let params = useParams();

    const [peopleDetails, setPeopleDetails] = useState(null);
    async function getPeopleDetails(id)
    {
        let {data} = await axios.get(`https://api.themoviedb.org/3/person/${id}?api_key=7498df51a2f5c06927536a5aa88482bd&language=en-Us`);
        setPeopleDetails(data);
    }

    useEffect(()=> {
        getPeopleDetails(params.id)
    }, [])

  return (
    <>
    {peopleDetails? <div className="row">
        <div className="col-md-3">
        <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + peopleDetails.profile_path}/>
        {/* <button className='btn btn-outline-warning w-100 my-1 p-2' ><Link to={peopleDetails.homepage}>{peopleDetails.original_title}</Link></button> */}
        </div>
        <div className="col-md-9">
            <h2>{peopleDetails.name}</h2>
            <p className='text-white-50 py-3'>{peopleDetails.overview}</p>
            <ul className='m-0 p-0'>
                {/* <li>original_title : {peopleDetails.original_title}</li> */}
                {/* <li>release_date : {peopleDetails.release_date}</li> */}
                <li>known_for_department : {peopleDetails.known_for_department}</li>
                <li>popularity : {peopleDetails.popularity}</li>
                {/* <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + peopleDetails.backdrop_path}/> */}

                {/* <li>homepage : {peopleDetails.homepage}</li> */}
            </ul>
        </div>
    </div>:<div className='vh-100 d-flex align-items-center justify-content-center'>
        <i className='fas fa-spinner fa-spin fa-3x'></i>
        </div>}
    </>
  )
}
