import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom';


export default function Tv() {

  const [trendingTv, setTrendingTv] = useState([]);

  let nums = new Array(13).fill(1).map(( elem, index)=> index+1);

  async function getTrending(pageNumber) {

    let {data} = await axios.get(`https://api.themoviedb.org/3/discover/tv?api_key=7498df51a2f5c06927536a5aa88482bd&language=en-US&sort_by=popularity.desc&include_adult=false&include_video=false&page=${pageNumber}`);
    setTrendingTv(data.results)
  }

  useEffect(()=> {
    getTrending(1);
  },[])

  return (
    <>

    {trendingTv? <div className="row justify-content-center">
        {trendingTv.map(( tv, i)=> <div key={i} className='col-md-2'>
          <div className="tv">
            <Link to={`/tvdetails/${tv.id}`}>
              <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + tv.poster_path}/>
              <h3 className='h6 my-2 p-2'>{tv.name}</h3>
            </Link>
          
          </div>
        </div>)}
      </div>:<div className='vh-100 d-flex align-items-center justify-content-center'>
        <i className='fas fa-spinner fa-spin fa-3x'></i>
        </div>}



        <nav aria-label="...">
  <ul className="pagination pagination-sm d-flex justify-content-center flex-wrap ">
    {nums.map((pageNum ,i)=> <li onClick={()=> getTrending(pageNum)} key={i} className="page-item"><a className="page-link bg-transparent text-white">{pageNum}</a></li>)}
  </ul>

</nav>
    </>
  )
}
