import axios from "axios";
import { createContext, useEffect, useState } from "react";

export let moviesContext = createContext(0);

export default function MoviesContextProvider(props) {

  const [trendingMovies, setTrendingMovies] = useState([]);
  const [trendingTv, setTrendingTv] = useState([]);
  const [trendingPeople, setTrendingPeople] = useState([]);

  async function getTrending(mediaType, callback) {
    // mediaType => 'movie'
    // callback => trendingMovies
    let {data} = await axios.get(`https://api.themoviedb.org/3/trending/${mediaType}/week?api_key=7498df51a2f5c06927536a5aa88482bd`)
    callback(data.results.slice(0,10))
  }

  useEffect(()=> {
    getTrending('movie', setTrendingMovies);
    getTrending('tv', setTrendingTv);
    getTrending('person', setTrendingPeople);
  },[])

    return <moviesContext.Provider value={{trendingMovies, trendingTv, trendingPeople}}>
        {props.children}
    </moviesContext.Provider>
}
