import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'

export default function TvDetails() {

    let params = useParams();

    const [tvDetails, setTvDetails] = useState(null);
    async function getTvDetails(id)
    {
        let {data} = await axios.get(`https://api.themoviedb.org/3/tv/${id}?api_key=7498df51a2f5c06927536a5aa88482bd&language=en-Us`);
        setTvDetails(data);
    }

    useEffect(()=> {
        getTvDetails(params.id)
    }, [])

  return (
    <>
    {tvDetails? <div className="row">
        <div className="col-md-3">
        <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + tvDetails.poster_path}/>
        {/* <button className='btn btn-outline-warning w-100 my-1 p-2' ><Link to={tvDetails.homepage}>{tvDetails.original_title}</Link></button> */}
        </div>
        <div className="col-md-9">
            <h2>{tvDetails.name}</h2>
            <p className='text-white-50 py-3'>{tvDetails.overview}</p>
            <ul className='m-0 p-0'>
                {/* <li>original_title : {tvDetails.original_title}</li> */}
                <li>original Name : {tvDetails.original_name}</li>
                <li>popularity : {tvDetails.popularity}</li>
                <li>vote : {tvDetails.vote_average}</li>
                <li>vote_count : {tvDetails.vote_count}</li>
                {/* <img className='w-100' src={'https://image.tmdb.org/t/p/w500' + tvDetails.backdrop_path}/> */}

                {/* <li>homepage : {tvDetails.homepage}</li> */}
            </ul>
        </div>
    </div>:<div className='vh-100 d-flex align-items-center justify-content-center'>
        <i className='fas fa-spinner fa-spin fa-3x'></i>
        </div>}
    </>
  )
}
